@import '../../styles/vars';

.sidebar {
  position: relative;

  &-headband {
    width: 100%;
    position: fixed;
    background-color: $color-green;
    height: 50px;
    -webkit-box-shadow: 0px 10px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    transition: all .35s ease 0s;
    transform: translate3d(0px, 0px, 0px);
    z-index: 5;

    &-container {
      display: flex;
      justify-content: space-around;
      align-items: center;
    }

    &-logo {
      height: 50px;
      width: 200px;
      object-fit: cover;
    }

    &-search {
      cursor: pointer;
      font-size: 20px;
      color: $color-slateBlue;
    }
  }

  &-logo {
    display: block;
    margin: 0 auto;
  }

  &-mainContainer {
    transition: left 0.35s, right 0.35s;
    position: fixed;
    width: 255;
    height: calc(100% - 32px);
    z-index: 901;
  }

  &-container {
    transition: left 0.35s, right 0.35s;
    position: absolute;
    width: 255;
    height: calc(100% - 32px);
    z-index: 901;
  }

  &-hide {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.3) none repeat scroll 0% 0%;
    transition: all .35s ease 0s;
    transform: translate3d(-100%, 0px, 0px);
  }

  &-show {
    left: 0;
    position: fixed;
    right: inherit;
    z-index: 1100;
    width: 255px;
    height: 100vh;
    transition: all .35s ease 0s;
    background-color: $color-slateBlue;
  }

  &-menuList {
    margin-top: 52px;
  }

  &-burger {
    font-size: 24px;
    cursor: pointer;
    z-index: 1000;
    color: $color-slateBlue;

    &-on {
      position: absolute;
      width: 100vw;
      min-width: 100%;
      height: 100vh;
      transition: .35s ease;
      background-color: rgba(0,0,0,.50);
      z-index: 900;
    }
  }
}

.menuItem {
  height: 56px;
  cursor: pointer;
  padding-left: 32px;
  padding-right: 32px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;

  &:hover {
    background-color: rgba(221,226,255, 0.08);
    transition: .8s;
  }

  &-activeContainer {
    display: flex;
    height: 56px;
    cursor: pointer;
    padding-left: 32px;
    padding-right: 32px;
    transition: background-color 0.425s ease-in-out 0s;
    background-color: rgba(221, 226, 255, 0.08);
    border-left: 3px solid $color-blueDragee;
  }

  &-activeBar {
      height: 56;
      width: 3;
      background-color: $color-blueDragee;
      position: absolute;
      left: 0;
  }

  &-title {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #A4A6B3;
    margin-left: 24px;
  }

  &-activeTitle {
    color: $color-blueDragee;
  }

}

@media screen and (min-width: 768px) {
  .sidebar {

    &-headband {
      transition: all .35s ease 0s;
      transform: translate3d(-100%, 0, 0px);
    }

    &-mainContainer {
      height: 100%;
      min-height: 100vh;

      position: fixed;
      right: inherit;
      z-index: 1100;
      width: 255px;
    }

    &-hide {
      transform: none;
    }

    &-container {
      background-color: #363740;
      width: 255px;
      padding-top: 32px;
    }
  }
}